@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&family=Open+Sans&family=Poppins:ital,wght@0,200;1,200;1,300&family=Roboto&display=swap');

header {
    position: absolute;
    width: 996px;
    height: 291px;
    left: 222px;
    top: 147px;

    background: #FFFFFF;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
    border-radius: 16px;


}

header .images {
    width: 50%;
    float: left;
}

header .images img {
    position: absolute;
    width: 128px;
    height: 128px;
    left: 130px;
    top: 75px;

    border-radius: 120px;
}

header .wrapper {
    width: 50%;
    float: right;
}

header .wrapper h1 {
    font-family: 'Roboto', sans-serif;
    position: absolute;
    width: 600px;
    height: 64px;
    left: 320px;
    top: 10px;

    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 64px;
    line-height: 100%;
    /* identical to box height, or 64px */

    letter-spacing: -1.85px;
    text-decoration-line: underline;

    color: #E16259;

}


header .wrapper p{
    position: absolute;
width: 588px;
height: 87px;
left: 320px;
top: 105px;

font-family: 'Roboto';
font-style: normal;
font-weight: 700;
font-size: 20px;
line-height: 145%;
/* or 29px */

color: rgba(0, 0, 0, 0.6);


}


header .wrapper p .num{
   color:#A259FF;
}

header .wrapper p .place{
    color: black;
}

header .wrapper p .female{
    color: #8A8A8A;
}