.topic{
position: absolute;
width: 1200px;
height: 646px;
left: 80px;
top: 558px;

}

.topic{

display: grid;
grid-template-rows: 170px 170px 170px 170px 170px 170px;
grid-template-columns: 320px 320px 320px 320px;


}
.info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 12px 16px;
    margin: 15px;
    gap: 8px;

    width: 280px;
    height: 110px;

    background: #FFFFFF;
    /* Medium Shadow */

    box-shadow: 0px 2px 7px rgba(0, 0, 0, 0.2);
    border-radius: 4px;

    /* Inside auto layout */

    flex: none;
    order: 0;
    flex-grow: 0;

}

.info:hover{
    background-color: #A259FF;
}

.info p .gender {
    width: 31px;
    height: 21px;

    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    /* identical to box height, or 21px */


    color: rgba(0, 0, 0, 0.6);


    /* Inside auto layout */

    flex: none;
    order: 0;
    flex-grow: 0;
}

.info p .nat {
    width: 18px;
    height: 21px;

    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    /* identical to box height, or 21px */


    color: rgba(0, 0, 0, 0.6);


    /* Inside auto layout */

    flex: none;
    order: 2;
    flex-grow: 0;
}

.info h4 .fullname {
    position: relative;
    bottom: 35px;

    width: 250px;
    height: 30px;

    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    line-height: 135%;
    /* identical to box height, or 30px


    color: #FFFFFF;


    /* Inside auto layout */

    flex: none;
    order: 1;
    align-self: stretch;
    flex-grow: 0;
}

.info .email {
    position: relative;
    bottom: 75px;

    width: 217px;
    height: 23px;

    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 150%;
    /* identical to box height, or 22px */


    color: #E16259;


    /* Inside auto layout */

    flex: none;
    order: 1;
    flex-grow: 0;
}

.info:hover .gender{
    color: #FFFFFF;
}

.info:hover .nat{
    color: #FFFFFF;
}

.info:hover .fullname{
    color: #FFFFFF;
}

.info:hover .email{
    color: #FFFFFF;
}